.buttons-library {
  display: flex;
  position: relative;
  justify-content: center;
  gap: 4px;
  bottom: 70px;

  @media screen and (min-width: 768px) {
    position: relative;
    bottom: 300px;
    left: 10px;
  }

  @media screen and (min-width: 1280px) {
    position: relative;
    margin-left: 950px;
  }
}

.buttons-library button {
  background-color: rgba(255, 255, 255, 1);
  color: rgba(185, 47, 44, 1);
  border-radius: 15px;
  cursor: pointer;
}

.buttons-library button:focus {
  background-color: rgba(185, 47, 44, 1);
  color: rgba(255, 255, 255, 1);
  cursor: pointer;
}
.buttons-library button:hover {
  background-color: rgba(185, 47, 44, 1);
  color: rgba(255, 255, 255, 1);
  cursor: pointer;
}
.buttons-library .watched-tab-btn {
  padding: 10px 23px;
}

.buttons-library .queue-tab-btn {
  padding: 10px 24px;
}

.empty-library-message {
  display: flex;
  flex-direction: column;
  align-items: center;
}

//library_body_adi

.movie-library-container {
  display: flex;
  justify-content: center;
  padding: 32px;

  .movie-library-list {
    width: 1216px;
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    gap: 20px;
    flex-wrap: wrap;
    box-sizing: border-box;
    margin: 0;
    padding: 0;

    .card-movie {
      width: 395px;
      
      flex-basis: calc((100% - 64px) / 3);
      box-sizing: border-box;
      background-color: white;

      .movie-poster {
        width: 100%;
        height: 574px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;

        img {
          border-radius: 5px;
          width: 100%;
          height: 100%;
        }
      }

      .movie-name-container {
        padding-top: 12px;

        .movie-name {
          font-family: Roboto;
          font-size: 15px;
          font-weight: 500;
          line-height: 14px;
          letter-spacing: 0em;
          text-align: left;
          color: #000000;
          margin: 0;
        }
        .movie-genre-year {
          font-family: Roboto;
          font-size: 20px;
          font-weight: 500;
          line-height: 24px;
          letter-spacing: 0em;
          text-align: left;
          color: #b92f2c;
          margin: 0;
        }
      }
    }

    .card-movie:hover {
      scale: 1.01;
      transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
      border: 10px black;
      box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    }
  }
}
